._1o_H4 {
  color: #777;
  padding: 10px 15px;
  height: 20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}

._1o_H4:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2),
    0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}

._2_vlg {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  right: 0;
  left: 0;
}

._2_vlg li {
  display: inline;
}

._2_vlg li a {
  color: inherit;
  margin: 3px;
  padding: 3px 7px;
  text-decoration: none;
  border: 1px solid transparent;
  border-radius: 3px;
}

._2_vlg li a._1-_RW,
._2_vlg li a:hover {
  border-color: rgba(175, 47, 47, 0.1);
}

._2_vlg li a._1-_RW {
  border-color: rgba(175, 47, 47, 0.2);
}

._1HXXk {
  float: left;
  text-align: left;
}

._1HXXk strong {
  font-weight: 300;
}

._2-jVx,
html ._2-jVx:active {
  float: right;
  position: relative;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
  position: relative;
}

._2-jVx:hover {
  text-decoration: underline;
}

@media (max-width: 430px) {
  ._1o_H4 {
    height: 50px;
  }
  ._2_vlg {
    bottom: 10px;
  }
}
